.Navbar {
  position: absolute;
  background: rgba(69, 90, 100, 0.9);
  width: 100%;
  padding: 1.5rem;
  display: flex;
  justify-content: space-around;
}

.Navbar a {
  text-decoration: none;
  color: rgba(144, 164, 174, 1);
  transition: all 0.5s ease;
}
.Navbar a.Navbar-active {
  color: white;
}
